@import "../variables";

.population {
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    margin-right: 5px;
  }
}

.politicalLeaning {
  span {
    font-weight: bold;

    &.left {
      color: $politicalLeaningRed;
    }

    &.right {
      color: $politicalLeaningBlue;
    }

    &.centre {
      color: $politicalLeaningGrey;
    }
  }
}

.statistic {
  span {
    font-weight: bold;
  }
}

.modalOverlay {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: #333;
  opacity: 0.5;
  z-index: 5;
}

.container {
  position: fixed;
  z-index: 10;
  left: 0;
  right: 0;
  bottom: 0;
}

.modal {
  padding: 10px;
  overflow-y: scroll;
  text-align: center;
  border-radius: 7px;
  background-color: $white;
  box-shadow: 0px 0.5px 2px rgba(0, 0, 0, 0.25);
  height: 420px;
  color: $blue;

  @media (min-width: 400px) {
    height: 520px;
  }
}

@media (min-width: 820px) {
  .container {
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .modal {
    width: 820px;
    height: 620px;
  }
}
.header {
  margin-bottom: 20px;
}
.headerBackButton {
  position: absolute;

  button {
    width: 32px;
    height: 32px;
    padding: 0;
    background: #f5f5f5;
  }
}

.headerTitle {
  display: flex;
  justify-content: center;
  width: 100%;

  h1 {
    margin: 0;
    font-size: 1.4em;
  }
}

.body {
  display: flex;
  flex-direction: column;
  h2 {
    font-size: 1.2em;
  }
}

.statistics {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h2 {
    text-decoration: underline;
    font-size: $modalHeadingFontSizeSmall;
  }
}

.isInParty {
  font-style: italic;
}
